<template>
  <div>
    <v-stepper
      v-model="e1"
    >
      <v-stepper-header>
        <v-stepper-step
          :complete="false"
          step="1"
          @click="e1 = 1"
        >
          Информация о брони
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          :complete="false"
          step="2"
          @click="e1 = 2"
        >
          Данные клиента
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          step="3"
          :complete="false"
          @click="e1 = 3"
        >
          Услуги
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          step="4"
          :complete="false"
          @click="e1 = 4"
        >
          Стоимость
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          step="5"
          :complete="false"
          @click="e1 = 5"
        >
          Задачи
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          step="6"
          :complete="false"
          @click="e1 = 6"
        >
          Подтверждение
        </v-stepper-step>

        <v-divider />
      </v-stepper-header>
      <v-stepper-items>
        <v-stepper-content step="1">
          <v-row>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-select
                v-model="$store.state.reservSource"
                :items="reservSource"
                label="Источник брони*:"
              />
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-menu
                ref="indatemenu"
                v-model="menu"
                :close-on-content-click="false"
                :return-value.sync="booking.inDate"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="$store.state.start"
                    label="Заезд*:"
                    readonly
                    v-bind="attrs"
                    prepend-icon="mdi-calendar-blank-multiple"
                    v-on="on"
                  />
                </template>
                <v-date-picker
                  v-model="$store.state.start"
                  no-title
                  scrollable
                >
                  <v-spacer />
                  <v-btn
                    text
                    color="primary"
                    @click="menu = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                    text
                    color="primary"
                    @click="$refs.indatemenu.save($store.state.start)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="$store.state.startTime"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="$store.state.startTime"
                    label="Время"
                    prepend-icon="mdi-alarm"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  />
                </template>
                <v-time-picker
                  v-if="menu2"
                  v-model="$store.state.startTime"
                  full-width
                  @click:minute="$refs.menu.save($store.state.startTime)"
                />
              </v-menu>
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="booking.reserveNumber"
                label="Номер брони у источника:"
                required
              />
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-menu
                ref="outDateMenu"
                v-model="menu3"
                :close-on-content-click="false"
                :return-value.sync="$store.state.end"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="$store.state.end"
                    label="Выезд*:"
                    readonly
                    v-bind="attrs"
                    prepend-icon="mdi-calendar-blank-multiple"
                    v-on="on"
                  />
                </template>
                <v-date-picker
                  v-model="$store.state.end"
                  no-title
                  scrollable
                >
                  <v-spacer />
                  <v-btn
                    text
                    color="primary"
                    @click="menu3 = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                    text
                    color="primary"
                    @click="$refs.outDateMenu.save($store.state.end)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-menu
                ref="outTimeMenu"
                v-model="menu4"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="$store.state.endTime"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="$store.state.endTime"
                    label="Время"
                    prepend-icon="mdi-alarm"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  />
                </template>
                <v-time-picker
                  v-if="menu4"
                  v-model="time2"
                  full-width
                  @click:minute="$refs.outTimeMenu.save($store.state.endTime)"
                />
              </v-menu>
            </v-col>

            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-row>
                <v-col
                  cols="6"
                >
                  <v-select
                    v-model="roomCategory"
                    :items="roomCategories"
                    label="Выберите номер*:"
                    >
                  </v-select>
                </v-col>
                <v-col>
                  <v-select
                    v-model="$store.state.room"
                    :items="roomCategory==='lux'?luxRooms:roomCategory==='halfLux'?halfLuxRooms:stdRooms"
                    label="Выберите номер*:"
                  >
                  </v-select>
                </v-col>
              </v-row>
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-row>
                <v-col>
                  <v-checkbox
                    v-model="$store.state.earlyIn"
                    label="Ранний заезд"
                  />
                </v-col>
                <v-col>
                  <v-checkbox
                    v-model="$store.state.latelyOut"
                    label="Поздний выезд"
                  />
                </v-col>
              </v-row>
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-col>
                <v-checkbox
                  v-model="$store.state.sleepingArea"
                  label="Доп. место"
                />
              </v-col>
              <v-col v-if="$store.state.sleepingArea">
                <v-select
                  :items="booking.sleepingAreaCount"
                  label="Выберите спальное место*:"
                />
              </v-col>
            </v-col>

            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              <v-select
                v-model="$store.state.status"
                :items="status"
                label="Статус:"
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              <v-select
                v-model="$store.state.noteType"
                :items="noteType"
                label="Метка:"
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              <v-text-field
                v-model="$store.state.category"
                label="Выбор категории номера:"
                required
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              <v-text-field
                v-model="$store.state.freeRoom"
                label="Свободный номер:"
                required
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              <v-text-field
                v-model="$store.state.cost"
                label="Стоимость проживания:"
                disabled
                required
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              Стандартная, со скидкой
            </v-col>
          </v-row>
          <v-btn
            color="primary"
            @click="e1 = 2"
          >
            Далее
          </v-btn>

          <v-btn
            text
            @click="e1 = 1"
          >
            Назад
          </v-btn>
        </v-stepper-content>
        <v-stepper-content step="2">
          <v-form
            ref="form"
            v-model="valid"
          >
            <v-row>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="lastName"
                  :rules="$store.state.status === 'pre-booking'
                    ?[v => !!v || 'Обязательное поле для заполнения ']
                    :[]"
                  label="Фамилия:"
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="firstName"
                  label="Имя:"
                  :rules="$store.state.status === 'pre-booking'
                    ?[v => !!v || 'Обязательное поле для заполнения ']
                    :[]"
                  required
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="patronymic"
                  label="Отчество:"
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="phone"
                  label="Телефон:"
                  :rules="$store.state.status==='pre-reserve' || $store.state.status === 'reserve' || $store.state.status === 'pre-booking'
                    ?[v => !!v || 'Обязательное поле для заполнения ']
                    :[]"
                  required
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="email"
                  label="e-mail:"
                  :rules="$store.state.status==='pre-reserve' || $store.state.status === 'reserve' || $store.state.status === 'pre-booking'
                    ?[v => !!v || 'Обязательное поле для заполнения ']
                    :[]"
                  required
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-text-field
                  v-model="passport"
                  label="Паспорт:"
                  :rules="$store.state.status === 'pre-booking'
                    ?[v => !!v || 'Обязательное поле для заполнения ']
                    :[]"
                  required
                />
              </v-col>
              <v-col
                cols="12"
                md="4"
              >
                <v-select
                  v-model="gender"
                  :items="genders"
                  :rules="$store.state.status === 'pre-booking'
                    ?[v => !!v || 'Обязательное поле для заполнения ']
                    :[]"
                  required
                />
              </v-col>
            </v-row>
          </v-form>
          <v-btn
            color="primary"
            @click="e1 = 3"
          >
            Далее
          </v-btn>
          <v-btn
            text
            @click="e1 = 1"
          >
            Назад
          </v-btn>
        </v-stepper-content>
        <v-stepper-content step="3">
          <v-simple-table>
            <thead>
              <tr>
                <th class=" primary--text">
                  Услуга
                </th>
                <th class=" primary--text">
                  Цена
                </th>
                <th class=" primary--text">
                  Тип
                </th>
                <th class=" primary--text">
                  Количество
                </th>
                <th class=" primary--text">
                  К оплате
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td class="">
                  Обед
                </td>
                <td
                  class=""
                >
                  <v-text-field
                    v-model="$store.state.services.lunch"
                    color="primary"
                  />
                </td>
                <td>
                  единоразово
                </td>
                <td>
                  <v-text-field
                    color="primary"
                    type="number"
                  />
                </td>
                <td>
                  {{ $store.state.cost }}
                </td>
              </tr>
              <tr>
                <td class="">
                  Дополнительное место
                </td>
                <td
                  class=""
                >
                  <v-text-field
                    v-model="$store.state.services.additionalSeats"
                    color="primary"
                  />
                </td>
                <td>
                  в сутки
                </td>
                <td>
                  <v-text-field
                    color="primary"
                    type="number"
                  />
                </td>
                <td>
                  {{ $store.state.cost }}
                </td>
              </tr>
              <tr>
                <td class="">
                  Прокат велосипедов
                </td>
                <td
                  class=""
                >
                  <v-text-field
                    v-model="$store.state.services.rentalBike"
                    color="primary"
                  />
                </td>
                <td>
                  единоразово
                </td>
                <td>
                  <v-text-field
                    color="primary"
                    type="number"
                  />
                </td>
                <td>
                  {{ $store.state.cost }}
                </td>
              </tr>
            </tbody>
          </v-simple-table>
          <v-btn
            color="primary"
            @click="e1 = 4"
          >
            Далее
          </v-btn>
          <v-btn
            text
            @click="e1 = 2"
          >
            Назад
          </v-btn>
        </v-stepper-content>
        <v-stepper-content step="4">
          Итого
          <p>Суток: 3</p>
          <p>За проживание: 0</p>
          <p>За услуги: 0</p>
          <p>К оплате: 0</p>
          <p>Оплачено: 0</p>
          <p>Осталось оплатить: 0</p>
          <v-divider />
          <v-row>
            <v-col
              cols="3"
            >
              <v-text-field
                class="float-left"
                outlined
                label="Скидка"
                max-width="200"
              />
            </v-col>
            <v-col
              cols="1"
            >
              <span class="ml-3 float-left">Тип</span>
            </v-col>
            <v-col
              cols="3"
            >
              <v-select
                :items="types"
                max-width="50"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col
              cols="3"
            >
              <v-text-field
                class="float-left"
                outlined
                label="К оплате:"
                max-width="200"
              />
            </v-col>
            <v-col
              cols="7"
            >
              <v-text-field
                class="float-left"
                outlined
                label="Оплачено:"
              />
            </v-col>
          </v-row>
          <v-container>
            <v-row>
              <v-btn
                color="primary"
                @click="e1 = 5"
              >
                Далее
              </v-btn>

              <v-btn
                text
                @click="e1 = 3"
              >
                Назад
              </v-btn>
            </v-row>
          </v-container>
        </v-stepper-content>
        <v-stepper-content step="5">
          <v-row>
            <v-col
              cols="6"
            >
              <v-container>
                <v-row>
                  <v-select
                    v-model="task"
                    :items="tasks"
                    outlined
                    max-width="50"
                    label="Новая задач"
                  />
                </v-row>
                <v-row>
                  <v-text-field
                    v-model="whatToDo"
                    label="Что сделать?"
                  />
                </v-row>
                <v-row>
                  <v-textarea
                    v-model="desc"
                    label="Описание"
                  />
                </v-row>
              </v-container>
            </v-col>
            <v-col
              cols="6"
            >
            </v-col>
          </v-row>
          <v-container>
            <v-row>
              <v-btn
                color="primary"
                @click="e1 = 6"
              >
                Далее
              </v-btn>
              <v-btn
                @click="e1 = 4"
              >
                Назад
              </v-btn>
            </v-row>
          </v-container>
        </v-stepper-content>
        <v-stepper-content step="6">
          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="$store.state.client.lastName"
                label="Фамилия:"
                disabled
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="$store.state.client.firstName"
                label="Имя:"
                disabled
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="$store.state.client.patronymic"
                label="Отчество:"
                disabled
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-select
                v-model="$store.state.reservSource"
                :items="reservSource"
                disabled
                label="Источник брони*:"
              />
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                v-model="$store.state.start"
                label="Заезд*:"
                readonly
                disabled
                prepend-icon="mdi-calendar-blank-multiple"
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="$store.state.startTime"
                label="Время"
                prepend-icon="mdi-alarm"
                readonly
                disabled
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="booking.reserveNumber"
                label="Номер брони у источника:"
                disabled
              />
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                v-model="$store.state.end"
                label="Выезд*:"
                readonly
                disabled
                prepend-icon="mdi-calendar-blank-multiple"
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="$store.state.endTime"
                label="Время"
                prepend-icon="mdi-alarm"
                readonly
                disabled
              />
            </v-col>

            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-row>
                <v-col
                  cols="6"
                >
                  <v-select
                    v-model="roomCategory"
                    :items="roomCategories"
                    label="Выберите номер*:"
                    disabled
                  >
                  </v-select>
                </v-col>
                <v-col>
                  <v-select
                    disabled
                    v-model="$store.state.room"
                    :items="roomCategory==='lux'?luxRooms:roomCategory==='halfLux'?halfLuxRooms:stdRooms"
                    label="Выберите номер*:"
                  >
                  </v-select>
                </v-col>
              </v-row>
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-row>
                <v-col>
                  <v-checkbox
                    v-model="$store.state.earlyIn"
                    label="Ранний заезд"
                    disabled
                  />
                </v-col>
                <v-col>
                  <v-checkbox
                    v-model="$store.state.latelyOut"
                    label="Поздний выезд"
                    disabled
                  />
                </v-col>
              </v-row>
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="4"
            >
              <v-col>
                <v-checkbox
                  disabled
                  v-model="$store.state.sleepingArea"
                  label="Доп. место"
                />
              </v-col>
              <v-col v-if="$store.state.sleepingArea">
                <v-select
                  disabled
                  :items="booking.sleepingAreaCount"
                  label="Выберите спальное место*:"
                />
              </v-col>
            </v-col>

            <v-col
              class="d-flex"
              cols="12"
              sm="4"
              md="4"
            >
              <v-select
                v-model="$store.state.status"
                :items="status"
                disabled
                label="Статус:"
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="4"
              md="4"
            >
              <v-select
                disabled
                v-model="$store.state.noteType"
                :items="noteType"
                label="Метка:"
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="4"
              md="4"
            >
              <v-text-field
                disabled
                v-model="$store.state.category"
                label="Выбор категории номера:"
                required
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              <v-text-field
                disabled
                v-model="$store.state.freeRoom"
                label="Свободный номер:"
                required
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              <v-text-field
                v-model="$store.state.cost"
                label="Стоимость проживания:"
                disabled
                required
              />
            </v-col>
            <v-col
              class="d-flex"
              cols="12"
              sm="6"
              md="6"
            >
              Стандартная, со скидкой
            </v-col>
          </v-row>
        </v-stepper-content>
        <v-divider />
        <v-container>
          <v-btn
            color="primary"
            @click="reserveValidate()"
            v-if="$store.state.status==='pre-reserve' || $store.state.status==='empty' || $store.state.status===''"
          >
            Резерв
            </v-btn>
          <v-btn
            color="primary"
            @click="bookingValidate()"
            v-if="$store.state.status==='reserve'|| $store.state.status==='pre-booking' || $store.state.status===''"
          >
            Бронировать
          </v-btn>
          <v-btn
            color="primary"
            @click="setPopulate()"
            v-if="$store.state.status==='booking' || $store.state.status==='half-booking' || $store.state.status===''"
          >
            Заселить
          </v-btn>
          <v-btn
            color="primary"
            v-if="$store.state.status==='populate'"
            @click="$store.commit('UNSET_POPULATE')"
          >
            Выселить
          </v-btn>
          <v-btn
            color="info"
          >
            Редактировать
          </v-btn>
          <v-btn
            color="danger"
            @click="annulate()"
          >
            Аннулировать
          </v-btn>
          <v-btn
            color="danger"
          >
            Удалить
          </v-btn>
          <v-btn
            color="danger"
          >
            Отправить
          </v-btn>
        </v-container>
      </v-stepper-items>
    </v-stepper>

    <v-snackbar
      v-model="$store.state.snackbar"
      :bottom="'bottom'"
      :color="'red'"
      :left="'left'"
      :timeout="6000"
    >
      {{ $store.state.text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          dark
          text
          v-bind="attrs"
          @click="$store.state.snackbar = false"
        >
          X
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

